












import Component from "vue-class-component";
import Vue from "vue";
import { Model, Prop } from "vue-property-decorator";

@Component({})
export default class AdvancedSelect<T> extends Vue {
    @Model("input")
    initialValue: T | null;

    @Prop({ required: true })
    initialOptions: T[];

    @Prop({ required: false, default: () => false })
    noSelectionAllowed: boolean;

    @Prop({ required: false, default: () => null })
    title: string | null;

    @Prop({ required: false, default: () => null })
    label: (T) => string;

    @Prop({ required: false, default: () => null })
    trackBy: (T) => string;

    value: T | null;

    data() {
        return {
            value: this.initialValue
        };
    }

    get options(): Array<T | null> {
        return this.noSelectionAllowed
            ? [null, ...this.initialOptions]
            : this.initialOptions;
    }

    createOptionLabel(option: T): T | string {
        if (this.label == null) {
            return option;
        }
        return this.label(option);
    }

    createOptionTrackBy(option: T): T | string {
        if (this.trackBy == null) {
            return option;
        }
        return this.trackBy(option);
    }

    onChange() {
        this.$emit("input", this.value);
    }
}
